import React,{Component} from "react";
import axios from "axios";
axios.defaults.baseURL="http://60.205.215.60:7776"
import layer from "../../../../assets/utils/layer/mobile/layer.js"
import {browserHistory} from "react-router" 


export default class Login extends Component{

    goback=()=>{
        this.props.router.go(-1);
    }

    register=()=>{
        var username=this.refs.username.value;
        var password=this.refs.password.value;
        var phone=this.refs.phone.value;
        var that=this;

     

        if(!username||!password||!phone){
            layer.open({
                content:"",
                time:3
            })

        }
        else if(!/^[a-zA-Z]\w{5,17}$/.test(password)){
            layer.open({
                content:"6~18",
                time:3
            })
        }else if(!/^(13[0-9]|14[5|7]|15[0|1|2|3|5|6|7|8|9]|18[0|1|2|3|5|6|7|8|9])\d{8}$/.test(phone)){
            layer.open({
                content:"",
                time:3
            })
        }else{
            
           
            axios.get("/register",{params:{
                username,
                password,
                phone
            }})
              .then(res=>{
                  console.log(res.data)
    
                  if(res.data=="1"){
    
                      layer.open({
                          content:"",
                          time:3
                      })
    
    
                      setTimeout(function(){
                        that.props.router.push("/login");
    
                      },2000)
                
                  }else{
                     
                      layer.open({
                        content:"",
                        time:3
                    })
                  }
              })
            

        }

       
    }

    getma=()=>{

        
        layer.open({
            content:"1111",
            time:4
        })

    }


    
    render(){
        return(
            <div className="register">
                 <div className="register-head">
                    <span onClick={this.goback}>
                        <i className="iconfont icon-arrow-right"></i>
                    </span>
                    <p></p>
                </div>

                <div className="register-content">
                    <div className="top">
                        <span></span>
                    </div>
                    <div className="center">
                        <div>
                        <input type="text" placeholder="" ref="username"/>

                        </div>
                        <div>
                        <input type="password" placeholder="" ref="password"/>

                        </div>
                        <div>
                        <input type="text" placeholder="" ref="phone"/>
                          <span onClick={this.getma}></span>
                        </div>
                        <div>
                        <input type="text" placeholder=""/>

                        </div>
               
                        
                    </div>
                    <div className="bottom">

                        <input type="button" value=""  onClick={this.register}/>
                    </div>


                </div>




            </div>
        )
    }
}



