import React,{Component} from "react";
import {Link} from "react-router";
import {connect} from "react-redux";
import { Modal, Button,message } from 'antd';
import axios from "axios"
axios.defaults.baseURL="http://60.205.215.60:7776"
import layer from "../../../../assets/utils/layer/mobile/layer.js"
import {browserHistory} from "react-router"

@connect()

export default class Login extends Component{

    goback=()=>{
        this.props.router.push("/my");
    }

    login=()=>{

        var username=this.refs.username.value;
        var password=this.refs.password.value;
        var that=this;

     

        if(!username||!password){
            layer.open({
                content:"",
                time:3
            })

        }else if(!/^(13[0-9]|14[5|7]|15[0|1|2|3|5|6|7|8|9]|18[0|1|2|3|5|6|7|8|9])\d{8}$/.test(username)){
            layer.open({
                content:"",
                time:3
            })
        }else if(!/^[a-zA-Z]\w{5,17}$/.test(password)){
            layer.open({
                content:"6~18",
                time:3
            })
        }else{

            
        axios.get("/login",{
            params:{
                password:password,
                username:username
            }
        })
           .then(res=>{
             
               if(res.data=="1"){
                layer.open({
                    content:"",
                    time:3
                })

                setTimeout(function(){
                    that.props.router.push("/my");

                },2000)

                  localStorage.username=username;
               }else{
                layer.open({
                    content:"",
                    time:3
                })
               }
           })


        }

   
    }

    render(){
        return(
            <div className="login">
                <div className="login-head">
                    <span onClick={this.goback}>
                        <i className="iconfont icon-arrow-right"></i>
                    </span>
                    <p></p>
                </div>
                <div className="login-content">
                 <div className="top">
                     <div>
                         <Link to="/">
                             <i className="iconfont icon-weixin"></i>
                             <span></span>
                         </Link>

                         <Link to="/">
                             <i className="iconfont icon-qq"></i>
                             <span>QQ</span>
                         </Link>

                         <Link to="/">
                             <i className="iconfont icon-weibo1"></i>
                             <span></span>
                         </Link>
                         
                        </div>

                         <p></p>

                   

                 </div>
                 <div className="input">
                    <input type="text" placeholder="" ref="username"/>
                    <input type="password" placeholder="" ref="password"/>

                 </div>
                 <div className="bottom">
                     
                     <Link to="/register"></Link>
                     <Link to="/"></Link>

                 </div>

                 <div className="submit">

                     <button onClick={this.login}></button>

                 </div>


                </div>

               
            </div>
        )
    }
}



