import React,{Component} from "react";
import {connect} from "react-redux";
import {change_touxiang} from "../../actions";

@connect(

)

export default class Mycenter extends Component{


    goback=()=>{                                   
        this.props.router.go(-1);

    }

    outlogin=()=>{
        const {dispatch} =this.props;
        var a="touxiangno"
        localStorage.clear();
        dispatch(change_touxiang(a))
        this.props.router.push("/my")

    }
    render(){
        return(
            <div className="mycenter">

                <div className="mycenter-head">
                    <i className="iconfont icon-arrow-right" onClick={this.goback}></i>
                    <p></p>
               
                </div>
                <div className="mycenter-bottom">
                 <ul>
                     <li>
                        <div className="left"></div>
                        <div className="rigth">
                       
                        <i className="iconfont icon-arrow-right"></i>
                        <img src={require("../../../../assets/images/3.png")}/>
                        </div>

                     </li>

                     <li>
                     <div className="left"></div>
                        <div className="rigth">
                        <span></span>
                        <i className="iconfont icon-arrow-right"></i>
                        </div>

                     </li>

                     <li>
                     <div className="left"></div>
                        <div className="rigth">
                        <span></span>
                        <i className="iconfont icon-arrow-right"></i>
                        </div>

                     </li>

                     
                     <li>
                     <div className="left"></div>
                        <div className="rigth">
                        <span></span>
                        <i className="iconfont icon-arrow-right"></i>
                        </div>

                     </li>

                     
                     <li>
                     <div className="left"></div>
                        <div className="rigth">
                        <span> </span>
                        <i className="iconfont icon-arrow-right"></i>
                        </div>

                     </li>
                 </ul>



                  
                
                

                
                </div>

                <div className="submit">
                   <button onClick={this.outlogin}></button>

                </div>


                

            </div>
        )
    }
}


