import React,{Component} from "react";
import {Link} from "react-router";

export default class Minsu extends Component{
    constructor(props){
       super(props);
       this.state={
           footList:[
               {path:"/home",txt:"",icon:"icon-icon"},
               {path:"/minsu",txt:"",icon:"icon-minsu"},
               {path:"/bangdan",txt:"",icon:"icon-bangdan"},
               {path:"/my",txt:"",icon:"icon-wode"},
           ]
       }
    }

    render(){
        const {footList} =this.state;
        return(
            <div className="foot">
                {
                  footList.map((item,idx)=>{
                      return(
                          <Link to={item.path} key={idx} activeClassName="active">
                          <i className={"iconfont "+item.icon}></i>
                          <span>{item.txt}</span>
                          </Link>
                      )
                  })
                }

            </div>
        )
    }
}


